import React, { useMemo } from 'react';
import { Post } from 'models/post';
import { Paper, Grid, Typography, Box, makeStyles, Theme } from '@material-ui/core';
import format from 'date-fns/format';

const useStyle = makeStyles<Theme>(({ spacing, breakpoints, shape }) => ({
  root: {
    padding: spacing(2.5),
  },
  infoWrapper: {
    order: 2,
    [breakpoints.up('md')]: {
      order: 1,
    },
  },
  thumbnailWrapper: {
    order: 1,
    [breakpoints.up('md')]: {
      order: 2,
    },
    '& > div': {
      position: 'relative',
      width: '100%',
      paddingBottom: '70%',
      [breakpoints.up('md')]: {
        width: 120,
        paddingBottom: '100%',
      },
    },
  },
  thubmnail: {
    position: 'absolute',
    objectFit: 'cover',
    width: '100%',
    height: '100%',
    borderRadius: shape.borderRadius,
  },
  title: {
    fontWeight: 'bold',
  },
  date: {
    marginTop: spacing(1),
    fontWeight: 200,
  },
}));

interface Props {
  post: Post;
  onClick?: () => void;
}
const PostIntroCard = ({ post, onClick }: Props) => {
  const styles = useStyle();

  const formattedDate = useMemo(() => {
    const date = new Date(post.frontmatter.date);
    return format(date, 'yyy MMM dd');
  }, [post.frontmatter.date]);

  return (
    <Paper variant="outlined" className={styles.root} onClick={onClick}>
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12} md className={styles.infoWrapper}>
          <Box>
            <Typography gutterBottom variant="body1" component="h2" className={styles.title}>
              {post.frontmatter.title}
            </Typography>
            <Typography gutterBottom component="p" variant="caption">
              {post.excerpt}
            </Typography>
            <Typography variant="caption" component="p" color="textSecondary" className={styles.date}>
              {formattedDate}
            </Typography>
          </Box>
        </Grid>
        <Grid item xs={12} md="auto" className={styles.thumbnailWrapper}>
          <div>
            <img
              src={post.frontmatter.thumbnail?.childImageSharp.fluid?.src}
              alt={post.frontmatter.title}
              className={styles.thubmnail}
            />
          </div>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default PostIntroCard;
